import React from "react";
import { Link } from "react-router-dom";

const homeUrl = process.env.PUBLIC_URL;

function Home() {
    return(
        <div className="App">
            <header className="App-header">
                <p>Home</p>
                <div>
                    Stepn page is <Link to={homeUrl + `/stepn`}>Stepn</Link>.
                </div>
            </header>
        </div>
    );
}

export default Home;