import React from "react";
import { Link } from "react-router-dom";

const homeUrl = process.env.PUBLIC_URL;

function Error() {
    return(
        <div className="App">
            <header className="App-header">
                <p>Error</p>
                <div>
                    Home page is <Link to={homeUrl}>Hear</Link>.
                </div>
            </header>
        </div>
    );
}

export default Error;