import React, { useState, useEffect, useCallback} from 'react';

const Stepn = () => {
    const Data = {status:{loop:false},price:{gst_sol:0,gst_bnb:0,gst_eth:0,gmt:0,sol:0,bnb:0,eth:0}};
    const [data, setData] = useState(Data);

    const axios = require("axios").default;
    const url = [
        ["GST_SOL","https://cors-konamimi.herokuapp.com/https://coinmarketcap.com/ja/currencies/green-satoshi-token/"],
        ["GST_BNB", "https://cors-konamimi.herokuapp.com/https://coinmarketcap.com/ja/currencies/green-satoshi-token-bsc/"],
        ["GMT", "https://cors-konamimi.herokuapp.com/https://coinmarketcap.com/ja/currencies/green-metaverse-token/"],
        ["SOL", "https://cors-konamimi.herokuapp.com/https://coinmarketcap.com/ja/currencies/solana/"],
        ["BNB", "https://cors-konamimi.herokuapp.com/https://coinmarketcap.com/ja/currencies/bnb/"],
        ["ETH", "https://cors-konamimi.herokuapp.com/https://coinmarketcap.com/ja/currencies/ethereum/"]        
    ];

    const cheerio = require("cheerio");

    function sleep(milliseconds) {
        return new Promise((resolve) => setTimeout(resolve, milliseconds));
    }

    async function Start() {
        handleUpdate();
    }

    async function Loop() {
        for(let i=0; i<20; i++){
            try {
                console.log(i);
                await handleUpdate();
                await handleCheck();
                await sleep(10000);

            } catch(e) {
                console.log("error");
                await sleep(1000);
            }
        }
        console.log("loop end");
    }

    const handleCheck  = () => {
        console.log(data.price);
    }

    const handleUpdate = useCallback(async () => {
        const updates = {...data};
        for (let i = 0; i < url.length; i++) {
            const response = await fetchData(url[i]);
            //console.log(url[i][0], response);
            switch (url[i][0]){
                case "GST_SOL":
                    updates.price.gst_sol = response;
                    break;
                case "GST_BNB":
                    updates.price.gst_bnb = response;
                    break;
                case "GST_ETH":
                    updates.gst_eth = response;
                    updates.price.gst_eth = response;
                    break;
                case "GMT":
                    updates.gmt = response;
                    updates.price.gmt = response;
                    break;
                case "SOL":
                    updates.sol = response;
                    updates.price.sol = response;
                    break;
                case "BNB":
                    updates.bnb = response;
                    updates.price.bnb = response;
                    break;
                case "ETH":
                    updates.eth = response;
                    updates.price.eth = response;
                    break;
                default:
                    break;
            }
        }
        setData(updates);

        // const response = await fetchData(url[0]);
        // const updates = {...data};
        // updates.gst_sol = response;
        // console.log(response);
        // setData(updates);
    }, [])

    const fetchData = (async (url) => {
        //console.log("handleUpdate")
        if(!url){return Data};
        const {data} = await axios.get(url[1]);
        const $ = cheerio.load(data);
        const value = $('div.priceValue').text();
        return value;
    })
    
    useEffect( () => {
        //console.log("useEffect start");

        let unmounted = false;

        (async () => {
            if(!unmounted) {
                const fetchedData = await fetchData();
                setData(fetchedData);
            };
        })();

        return () => {unmounted = true;};
    }, []);


    return (
        <>
            <head><title>20220726</title></head>
            <p>Stepn Page</p>
            <button onClick={() => Start()}>
                取得
            </button>
            <button onClick={() => Loop()}>
                ループ
            </button>
            <div>GST_SOL:{data.price.gst_sol}</div>
            <div>GST_BNB:{data.price.gst_bnb}</div>
            <div>GST_ETH:nonData</div>
            <div>GMT:{data.price.gmt}</div>
            <div>SOL:{data.price.sol}</div>
            <div>BNB:{data.price.bnb}</div>
            <div>ETH:{data.price.eth}</div>
        </>
    );
}

export default Stepn;